.tool-box {
  width: 100%;
  height: 100%;
  background-color: #303030;
  padding-left: 2px;
  padding-right: 4px;
  border: 4px solid #191919;
  border-top: none;
  border-right: none;
  display: flex; }

.tool-box-large {
  flex-direction: row;
  align-items: center;
  overflow: hidden; }

.tool-box.tool-box-medium {
  flex-direction: column;
  overflow: hidden;
  height: 80px; }

.medium-toolbox-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  border-bottom: 3px solid #191919;
  width: 100%;
  height: 50%; }

.medium-toolbox-row:last-child {
  border-bottom: none; }

.tool-box:hover {
  overflow-x: auto; }

.tool-box-container {
  height: 100%;
  width: 100%; }

.tool-collection-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%; }

.toolbox-item {
  width: 30px;
  max-width: 30px;
  height: 30px;
  margin-left: 3px;
  margin-right: 3px; }

.toolbox-item.mobile {
  margin-right: 0; }

.tool-selector-item {
  margin-top: 3px;
  margin-bottom: 3px; }

.tool-selector-item:last-child {
  margin-bottom: 0; }

.toolbox-actions-center {
  float: left;
  display: flex;
  justify-content: center;
  align-items: center; }

.color-container {
  cursor: pointer;
  width: calc(30px * .85);
  height: calc(30px * .85);
  float: left;
  min-width: calc(30px * .85);
  align-items: center;
  display: flex;
  overflow: hidden; }

#tool-box-stroke-color-button {
  border-radius: 20px; }

#tool-box-fill-color-button {
  border-radius: 20px; }

#stroke-color-picker-container {
  box-sizing: border-box; }

.toolbox-actions-right {
  display: flex;
  flex-direction: row;
  align-items: center;
  float: right; }

.toolbox-actions-right {
  display: flex;
  justify-content: center;
  align-items: center; }

.toolbox-actions-right-container {
  display: flex;
  flex-direction: row;
  margin-left: auto;
  align-items: center;
  height: 100%; }

.bump-up-no-dropdown {
  margin-bottom: 2.5px; }

#settings-panel-container {
  display: flex;
  justify-content: center;
  background-color: transparent;
  align-items: center; }

.settings-input-container {
  display: flex;
  justify-content: center;
  align-items: center; }

.brush-modes-widget {
  height: 35px;
  display: flex;
  align-items: center;
  margin: 1px; }
