.inspector {
  background-color: #303030;
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  border-right: 4px solid #191919;
  font-family: 'Nunito Sans';
  overflow: hidden; }

.inspector-body {
  height: calc(100% - 36px);
  width: 100%;
  overflow: hidden; }

.inspector-body:hover {
  overflow-y: auto; }

.input-divider {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 20px;
  font-family: 'Nunito Sans';
  display: flex;
  align-items: center;
  justify-content: center; }

.inspector-item {
  padding-left: 10px;
  padding-right: 10px;
  padding-top: calc(10px / 2);
  padding-bottom: calc(10px / 2);
  border-bottom: calc(4px * .5) solid #191919;
  display: flex;
  flex-direction: column;
  align-items: center; }

.select-inspector {
  height: 20px; }

.inspector-action-row {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 4px; }

.inspector-script-window-row-container {
  height: 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-radius: 3px;
  margin-top: 5px;
  margin-left: 5px;
  margin-right: 5px; }

.inspector-script-window-container {
  width: 100%;
  height: 100px;
  background-color: #4A4A4A;
  border-radius: 3px; }

.inspector-script-window-container {
  height: 100%;
  padding-bottom: 5px; }

.inspector-script-window-header {
  width: 100%;
  height: 25px;
  background-color: #3B3B3B;
  color: #ffffff;
  text-align: center;
  vertical-align: center;
  border-radius: 3px 3px 0px 0px; }

.inspector-add-script-button {
  height: 25px;
  width: 100%; }

.inspector-script-window-row-name {
  width: calc(100% - 25px - 4px);
  height: 100%;
  background-color: #303030;
  color: white;
  border-radius: 3px;
  overflow: hidden;
  display: flex; }

.inspector-script-window-row-edit {
  width: 25px;
  height: 100%;
  background-color: #303030;
  color: white;
  border-radius: 3px; }

.inspector-script-window-row-delete {
  width: 25px;
  height: 100%;
  background-color: #303030;
  color: white;
  border-radius: 3px; }

.inspector-script-window-row-color-bar {
  height: 100%;
  width: 5px;
  margin-right: 5px; }

.inspector-script-window-row-color-bar.blue-bar {
  background-color: #05B8FF; }

.inspector-script-window-row-color-bar.green-bar {
  background-color: #29F1A3; }

.inspector-script-window-row-color-bar.yellow-bar {
  background-color: #FFE243; }
