#code-editor-resizeable {
  z-index: 1000;
  background-color: #464646;
  border: none;
  box-shadow: 2px 2px 5px;
  border-radius: 3px; }

.ace-editor {
  box-sizing: border-box; }

.code-editor-drag-handle {
  background-color: #292927;
  width: 100%;
  height: 40px;
  cursor: grab;
  display: flex;
  align-items: center;
  border-radius: 4px 4px 0px 0px;
  overflow: hidden; }

.code-editor-title {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #FFFFFF;
  float: left;
  height: 100%;
  left: 150px;
  position: relative;
  left: 150px; }

.code-editor-information {
  margin-left: 4px;
  color: #B5B5B5; }

.code-editor-information.code-editor-error-information {
  color: #FF9999; }

.code-editor-body {
  display: flex;
  position: absolute;
  width: 100%;
  height: calc(100% - 40px);
  overflow: hidden; }

.code-editor-code-panel {
  width: calc(100% - 150px);
  height: 100%;
  float: left; }

.code-editor-close-button {
  height: 25px;
  width: 25px;
  border: none;
  padding: none;
  margin-right: 10px;
  position: absolute;
  right: 0;
  background-color: #292927;
  cursor: pointer; }

.code-editor-unscriptable-warning {
  width: 100%;
  height: 100%;
  background-color: #919191;
  color: #626262;
  padding-left: 6px; }

.code-editor-thumbnail {
  position: absolute;
  z-index: 1;
  left: 50px;
  top: 15px;
  border-radius: 30px;
  width: 50px;
  height: 50px;
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.code-editor-tab-code-editor {
  height: 100%; }

#add-scripts-panel-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #3F3D3D;
  overflow: hidden; }

#code-editor-add-script-tab {
  width: 15px;
  height: 15px;
  display: flex;
  align-items: center;
  justify-content: center; }

#select-sub-tab-list {
  height: 40px;
  min-height: 40px;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: left;
  padding-left: 15px;
  overflow: auto;
  margin-bottom: 10px; }

.delete-tab-container {
  height: 17px;
  width: 17px;
  margin-left: 4px; }

.pop-out-add-scripts-body {
  margin-left: 10px;
  width: calc(100% - 10px); }

.react-tabs__tab {
  user-select: none; }

.add-script-container {
  overflow: auto;
  height: calc(100% - 40px - 10px);
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: flex-start;
  margin-top: 10px; }
